import myaxios from './myaxios'
const api={
    getcate:"/api/get_cate_list",
    getarticie:"/api/get_article_list",
    getyzm:"/user/sendSms",
    getreg:"/user/reg",
    getlogin:"/user/login",

  

}
export const getcate=()=> myaxios.get(api.getcate)
export const getarticie=(p)=> myaxios.post(api.getarticie,p)
export const getyzm=(p)=> myaxios.post(api.getyzm,p)
export const getreg=(p)=> myaxios.post(api.getreg,p)
export const getlogin=(p)=> myaxios.post(api.getlogin,p)
export const getuserInfoAPI = (p)=>myaxios.post('/user/getuserInfo',p);
export const gettokenAPI = ()=>myaxios.get('/upload/token');
export const getsendAPI = (p)=>myaxios.post('/api/add_article',p);
export const getsendlistAPI = (p)=>myaxios.get(`api/get_user_article_list?uid=${p}&skip&limit`,);
export const getdelsendAPI = (p)=>myaxios.post('/api/del_user_article',p);
export const getforgetAPI = (p)=>myaxios.post('/user/forget',p);
export const getupdateAPI = (p)=>myaxios.post('/user/updatePwd',p);
export const getsearchAPI = (p)=>myaxios.post('/api/search',p);
export const getfavAPI = (p)=>myaxios.post('/api/get_fav_list',p);
















