import Vue from "vue";
import VueRouter from "vue-router";
import welcome from "../views/welcome.vue";

Vue.use(VueRouter);
import users from "./modules/user"
import userAction from "./modules/userAction"

const routes = [
  {
    path: "/",
    name: "welcome",
    component: welcome,
  },
  {
    path: "/index",
    name: "index",
    meta: { root:true },
   
    component: () => import("../views/index.vue"),
  },
  {
    path: "/fabu",
    name: "fabu",
    meta: { root:true },
    component: () => import("../views/fabu.vue"),
  },
  {
    path: "/home",
    name: "home",
    meta: { root:true },
    component: () => import("../views/home.vue"),
  },
  {
    path: "/search",
    name: "search",

    component: () => import("../views/search.vue"),
  },
  {
    path: "/detailed",
    name: "detailed",

    component: () => import("../views/detailed.vue"),
  },

  ...users,
  ...userAction

  
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});
// let authPageLIst=["/fabu"];
